.main{
    min-height:100vh;
    max-width:100%;
    background-color:#c6e1ec;
    position:relative;}
.main::before,.main::after{
        content:'';
        position:absolute;
        left:0;
    }
    .main::before{
        clip-path:polygon(100% 33%,0% 100%,100% 100%);
        background:linear-gradient(45deg,rgba(216, 46, 8, 0.085),rgba(7, 119, 189, 0.126));
        width: 100%;
        height: 100%;
    }
   .main_container{
       display:flex;
       height:90vh;
       align-items: center;
       justify-content: space-between;
       position: relative;
   }
.main_content{
    margin-left:3.445rem;
}

.text{
    line-height:55px;
    color:green;
}
.text p{
    color:rgb(157,155,155);
    text-transform:uppercase;
    font-size:22px;
}
.text h1{
    font-size:50px;
    text-transform: uppercase;
}
.icons{
    display:flex;
}

.icons .icon{
    margin-top:1rem;
    margin-right:1.4rem;
    font-size:30px;
    cursor:pointer;
    
}

.buttons button{
    padding:15px 40px;
    margin-top:2rem;
    margin-right:1rem;
    background: linear-gradient(25deg,rgba(1, 60, 210, 0.753),rgb(255, 72, 6));
    color: #fff;
    border: 1px solid #fff;
    cursor: pointer;
    border-radius: 5px;
    text-transform: uppercase;
    font-weight: bolder;
}

.main_img {
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
    cursor: pointer;
}

.main img{
    background:linear-gradient(45deg,#ecefe7,rgb(62, 7, 189));
}

@media (max-width:1200px){
    .text{
        white-space: nowrap;
        line-height: 30px;
    }
    .text p{
        font-size: 14px;
    }
    .text .h1{
        font-size: 30px;
    }
    .buttons button {
        padding: 8px 20px;
    }

    .icons{
        margin-right:1rem;
    }
    .icons.icon {
        font-size: 25px;
    }
}
    
@media(max-width:600px){
    .main_content{
        margin-left: 0;
    }
    .main_container{
        text-align: center;
        flex-direction: column-reverse;
        justify-content: center;
    }
    .main_img img{
        width: 250px;
    }
    .text .h1{
        font-size: 24px;
    }
    .text p{
        color: #000;
        font-size: 11px;
    }
    .icons{
        margin-left: 1.5rem;
    }
    .icons .icon{
        color: firebrick;
    }
}
